.background-image {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('./images/fog01.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    animation: zoomIn 7s ease-out forwards;
    opacity: 1;
    transition: opacity 2s ease-in-out;
}

.background-image.fade-out {
  opacity: 0;
}

  @keyframes zoomIn {
    from {
      transform: scale(1) ;
    }
    to {
      transform: scale(1.2) ;
    }
  }

  